import { Component, OnInit, Injector, ViewChild, HostListener } from '@angular/core';
import { Router } from '@angular/router';
import * as _ from 'underscore';
import * as moment from 'moment';
import { CookieService } from 'ngx-cookie-service';
import { MeetingService } from 'src/app/api/controllers/Meeting';
import { ModalService } from 'src/app/shared/services/modal.service';
import { MatDialog } from '@angular/material/dialog';
import { jqxGridComponent } from 'src/app/shared/jqwidgets-scripts/jqwidgets-ts/angular_jqxgrid';
import { MeetingEditDisplayComponent } from '../meeting-edit-display/meeting-edit-display.component';
import { SharedService } from 'src/app/shared/services/shared.service';

@Component({
  selector: 'app-meetings-list',
  templateUrl: './meetings-list.component.html',
  styleUrls: ['./meetings-list.component.scss']
})
export class MeetingsListComponent implements OnInit {
  @ViewChild('datagrid', { static: false }) datagrid: jqxGridComponent;
  gridSourceData:any;
  columnData:any;
  lstMeetingData:any;
  isMeetingDataLoaded:boolean = false;
  isMobile:boolean = false;
  modalService:any;
  meetingFilter:string;


  constructor(private router: Router,
    private meetingService:MeetingService,
    private injector: Injector,
    private cookieService:CookieService,
    private sharedService:SharedService,
    public dialog: MatDialog) { 
    this.modalService = this.injector.get(ModalService)
  }

  @HostListener('window:oneditMeeting', ['$event.detail'])
  oneditMeeting(detail) {
    let dataRecord = this.datagrid.getrowdata(detail.rowId);
    let data:any = {
      type : 'edit',
      id : dataRecord.meetingId
    }
    const dialogRef = this.dialog.open(MeetingEditDisplayComponent, {
      width: 'auto',
      height:'700px',
      data:data
    });
    dialogRef.afterClosed().subscribe(result => {
      if(result) {
        this.getMeetingList();
      }
    });
  }

  @HostListener('window:ondeleteMeeting', ['$event.detail'])
  ondeleteSlot(detail) {
    let dataRecord = this.datagrid.getrowdata(detail.rowId);
    this.modalService.showConfirmModal(dataRecord.meetingId);
  }

  searchData() {
    if(this.meetingFilter != ""){
      let filtergroup = new jqx.filter();
      let filter_or_operator = 1;
      let filtervalue = this.meetingFilter;
      let filtercondition = 'contains';
      let filterData = filtergroup.createfilter('stringfilter', filtervalue, filtercondition);
      filtergroup.operator = 'or';
      filtergroup.addfilter(filter_or_operator, filterData);
      this.datagrid.showfiltercolumnbackground(false);
      this.columnData.forEach(item => {
        if(item.datafield != 'Actions') {
          this.datagrid.addfilter(item.datafield, filtergroup, true);
        }
      })
      this.datagrid.applyfilters();
    }
    else {
      this.datagrid.clearfilters();
    }
  }

  addMeeting() {
    this.router.navigate(['ams/meetings/create']);
  }

  getMeetingList() {
    let params = {
     apartmentId : parseInt(this.cookieService.get('apartmentId'))
    }
    this.meetingService.getMeetingByApartmentId(params).subscribe((res : any) => {
      if(res.length > 0) {
        this.lstMeetingData = res.filter((data)=> data.isActive);
        this.gridSourceData = {
          localdata: this.lstMeetingData.reverse(),
          datatype: "array"
        }
        this.lstMeetingData = new jqx.dataAdapter(this.gridSourceData);
      }
      this.isMeetingDataLoaded = true;
    },
    error => {
      console.log(error);
    });
  }

  getPrintParams(event){
    this.datagrid.exportdata(event, 'Meeting');
  }

  ngOnInit(): void {
    this.getMeetingList();
    var cellsrenderer = (row, column, value) => {
      return '<div class="jqx-custom-inner-cell">' + value + '</div>';
    }
    var columnrenderer = (value) => {
      return '<div style="padding: 14px">' + value + '</div>';
    }
    this.columnData = [{
        text: 'Date',
        datafield: 'meetingDate',
        cellsrenderer: (row, column, value) => {
          return '<div class="jqx-custom-inner-cell">'+ moment(value).format("DD-MM-YYYY")+'</div>'
        },
        minwidth: 100,
        renderer: columnrenderer
    },{
        text: 'Time',
        datafield: 'fromTime',
        cellsrenderer: (row, column, value) => {
          let time,fromTime,toTime = this.lstMeetingData.loadedData[row].toTime;
          if(value && toTime)  {
            fromTime = moment(value,'HH:mm:ss').format('hh:mm A');
            toTime = moment(toTime,'HH:mm:ss').format('hh:mm A');
            time = `${fromTime} - ${toTime}`
          } else {
            time = '-'
          }
          return '<div class="jqx-custom-inner-cell">'+ time +'</div>'
        },
        minwidth: 100,
        renderer: columnrenderer,
    },{
        text: 'Type',
        datafield: 'meetingTypeId',
        cellsrenderer: cellsrenderer,
        minwidth: 80,
        renderer: columnrenderer
    },{
        text: 'Subject',
        datafield: 'subject',
        cellsrenderer: cellsrenderer,
        minwidth: 180,
        renderer: columnrenderer
    },{
      text: 'Status',
      datafield: 'meetingStatusName',
      cellsrenderer: cellsrenderer,
      minwidth: 120,
      renderer: columnrenderer
    },{
      text: 'Actions',
      cellsalign: 'center', 
      align: 'center',
      cellsrenderer: (row: number): string => {
        return '<div class="simple-actions">'
        +'<a href="javascript:void(0)" class="mr-3" onClick="editMeeting('+ row +')"><i class="fa fa-pencil icon edit" aria-hidden="true"></i></a>'
        +'<a href="javascript:void(0)" class="mr-2" onClick="showConfirmDelete(' + row + ')"><i class="fa fa-trash icon delete" aria-hidden="true"></i></a>'
      },
      minwidth: 120,
      renderer: columnrenderer
    }];
      //delete item
    this.sharedService.unitlistdeleteindexcast.subscribe( item => {
      if(item != null){
        var params = {
          meetingId: item,
          deleteBy: parseInt(this.cookieService.get('userId'))
        }
        this.meetingService.deleteMeeting(params).subscribe((res:any) => {
          this.sharedService.setUnitListDeleteIndex(null);
          this.getMeetingList();
        });
      }
    });
  }
}

function editMeeting(row){
  var event = new CustomEvent('oneditMeeting', {
    detail: {
      rowId: row
    }
  })
  window.dispatchEvent(event);
}
(window as any).editMeeting = editMeeting;


function showConfirmDelete(row){
  var event = new CustomEvent('ondeleteMeeting', {
    detail: {
      rowId: row
    }
  })
  window.dispatchEvent(event);
}
(window as any).showConfirmDelete = showConfirmDelete;
