import { Component, OnInit, Injector } from '@angular/core';

import { LookupService } from 'src/app/api/controllers/Lookup';
import { SharedService } from '../../../../shared/services/shared.service';
import { CookieService } from 'ngx-cookie-service';
import { ModalService } from 'src/app/shared/services/modal.service';


@Component({
  selector: 'app-meetings-setup',
  templateUrl: './meetings-setup.component.html',
  styleUrls: ['./meetings-setup.component.scss']
})
export class MeetingsSetupComponent implements OnInit {
  
  meetingCategoryData:any;
  recepientsTypeData:any;

  isMeetingCategoryLoaded:boolean = false;
  isMeetingCategorySubmitted:boolean = true;

  isMeetingCategoryNew:boolean = false;
  isMeetingCategoryUpdate:boolean = false;

  meetingCategory:string = "";
  meetingCategoryUpdateId:number = 0;

  meetingRecepients:string = "";
  
  alertMeetingMessage:string = "";
  isCategoryError:boolean = false; 
  isCategorySuccess:boolean = false; 

  meeting:any;

  meetingCategoryDesc:string = "";
  isViewMode : boolean = false;
  modalService:any;

  constructor(
	private lookupService: LookupService,
	private injector: Injector,
    private sharedService:SharedService,
	private cookieService:CookieService) {
	this.modalService = this.injector.get(ModalService)
   }

  addNewMeetingCategory(){
	this.isCategorySuccess = this.isCategoryError = false;
	this.meetingCategoryDesc = this.meetingCategory = "";
  	this.isMeetingCategoryNew = true;
	this.isMeetingCategoryUpdate = false;
	this.isViewMode = false;
  }

  viewMeetingCategory(item){
	this.meetingCategory = this.meetingCategoryDesc = "";
	this.isCategorySuccess = this.isCategoryError = false;
  	this.isMeetingCategoryUpdate = true;
  	this.isMeetingCategoryNew = false;
	this.isViewMode = true;
	this.meetingCategory = item.lookupValueName;
	this.meetingCategoryDesc = item.description;  
  	this.meetingCategoryUpdateId = item.lookupValueId;
  }

  updateMeetingCategory(item){
	this.meetingCategory = this.meetingCategoryDesc = "";
	this.isCategorySuccess = this.isCategoryError = false;
  	this.isMeetingCategoryUpdate = true;
  	this.isMeetingCategoryNew = false;
	this.meetingCategory = item.lookupValueName;
	this.meetingCategoryDesc = item.description;  
	this.meetingCategoryUpdateId = item.lookupValueId;
	this.isViewMode = false;
  }

  deleteMeetingCategory(item, index){
	let params = {
		id:item.lookupValueId,
		index:index
	}
	this.modalService.showConfirmModal(params);
  }

  removeCategoryBox(){
  	this.isMeetingCategoryNew = false;
  	this.isMeetingCategoryUpdate = false;
  	this.isCategoryError = false;
	this.isCategoryError = false;
	this.isViewMode = false;
  }

  submitMeetingCategoryForm(form) {

  	this.isMeetingCategorySubmitted = false;

  	if(this.isMeetingCategoryNew) {

  		let details = {
		  "apartmentId" :  parseInt(this.cookieService.get('apartmentId')),
		  "lookupTypeId": 21, // Meeting category lookup id
		  "lookupValueName": this.meetingCategory,
		  "description": this.meetingCategoryDesc,
		  "isActive": true,
		  "insertedBy": parseInt(this.cookieService.get('userId')),
		  "insertedOn": new Date().toISOString(),
		  "updatedBy": null,
		  "updatedOn": null

		}

		let params = {
			lookupvalue: details
		}

		this.lookupService.addLookupValue(params).subscribe((res:any) => {

	  		if(res.message) {

				let params = {
					LookupTypeId: 21
				}
	  			
	  			this.lookupService.getLookupValueByLookupTypeId(params).subscribe((res:any) => {

	  				this.isMeetingCategorySubmitted = true;
					  this.isCategorySuccess = true;
						this.sharedService.setAlertMessage("Meeting Type Added Successfully!");
		          	this.meetingCategoryData = res.filter(item => {
	                	return item.isActive;
	              	});	

	  			});
				this.removeCategoryBox();

	  		}
	  		else {

	  			this.isMeetingCategorySubmitted = true;	
	          	this.isCategoryError = true;
	          	this.alertMeetingMessage = res.errorMessage;
	  		}

  		});

  	}
  	else {

  		let details = {
		  "apartmentId" :  parseInt(this.cookieService.get('apartmentId')),
  		  "lookupValueId": this.meetingCategoryUpdateId,
		  "lookupTypeId": 21, // Meeting category lookup id
		  "lookupValueName": this.meetingCategory,
		  "description": this.meetingCategoryDesc,
		  "isActive": true,
		  "insertedBy": parseInt(this.cookieService.get('userId')),
		  "insertedOn": new Date().toISOString(),
		  "updatedBy": null,
		  "updatedOn": null

		}

		let params = {
			lookupvalue: details
		}

		this.lookupService.updateLookupValue(params).subscribe((res:any) => {

	  		if(res.message) {

				let params = {
					LookupTypeId: 21
				}
	  			
	  			this.lookupService.getLookupValueByLookupTypeId(params).subscribe((res:any) => {

	  				this.isMeetingCategorySubmitted = true;
					  this.isCategorySuccess = true;
						this.sharedService.setAlertMessage("Meeting Type Updated Successfully!");
		          	
		          	this.meetingCategoryData = res.filter(item => {
	                	return item.isActive;
	              	});	

	  			});
				this.removeCategoryBox();
	  			

	  		}
	  		else {

	  			this.isMeetingCategorySubmitted = true;	
	          	this.isCategoryError = true;
	          	this.alertMeetingMessage = res.errorMessage;
	  		}

  		});

	} 
  }



  ngOnInit() {
  	this.meeting = {};
	let params = {
		LookupTypeId: 21
	}
  	this.lookupService.getLookupValueByLookupTypeId(params).subscribe((res:any) => {
  		this.isMeetingCategoryLoaded = true;
  		this.meetingCategoryData = res.filter(item => {
        	return item.isActive;
        });
  	});
	let meetingParams = {
		LookupTypeId: 22
	}
  	this.lookupService.getLookupValueByLookupTypeId(meetingParams).subscribe((res:any) => {
  		this.isMeetingCategoryLoaded = true;
  		this.recepientsTypeData = res.filter(item => {
        	return item.isActive;
        });
	});
	//delete item
	this.sharedService.unitlistdeleteindexcast.subscribe( item => {
		if(item != null) {
			this.isMeetingCategoryLoaded = false;
			var params = {
				lookupValueId: item.id,
				updateUserId: parseInt(this.cookieService.get('userId'))
			}
			this.lookupService.deleteLookupvalue(params).subscribe((res:any) => {
				this.sharedService.setUnitListDeleteIndex(null);
				this.meetingCategoryData.splice(item.index, 1);
				this.sharedService.setUnitListDeleteIndex(null);
				this.isMeetingCategoryLoaded = true;
				this.removeCategoryBox();
			});
		}
	});
	
  }

}
